    var url = location.href;
    url = url.indexOf('#') == -1 ? url : url.substring(0, url.indexOf('#')); //舍弃#及以后的数据
    var paraString = url.substring(url.indexOf("?") + 1, url.length).split("&"); //得到?号后面的数据
