module.exports = {
    data: function () {
        return {
            order: {},
            edit_success_tips: false
        };
    },
    created: function () {
        this.fetchData();
    },
    computed: {
        total_amount: function () {
            var total = 0;
            if (!this.order.items) {
                return 0;
            }
            for (var i = 0; i < this.order.items.length; i++) {
                var item = this.order.items[i];
                var sing = item.price * 100 * item.quantity / 100;
                total = (total * 100 + sing * 100) / 100;
            }
            return total;
        }
    },
    methods: {
        fetchData: function () {
            this.$http.get("http://api-test.shangcars.com" + '/order/' + this.$route.params.id + '?token=' + localStorage.token).then(function (res) {
                if (res.body.err_code == 0) {
                    this.order = res.body;
                }
            }, function (res) {});
        },
        detail_back: function () {
            window.history.go(-1);
        },
        detail_min: function (index) {
            var q = parseInt(this.order.items[index].quantity);
            this.order.items[index].quantity = q > 0 ? q - 1 : 0;
        },
        detail_add: function (index) {
            this.order.items[index].quantity = parseInt(this.order.items[index].quantity) + 1;
        },
        submit_edit: function () {
            var edit_items = [];
            for (var i = 0; i < this.order.items.length; i++) {
                var item = this.order.items[i];
                edit_items.push({ product_id: item.product_id, quantity: item.quantity });
            }

            //submit
            this.$http.put("http://api-test.shangcars.com" + '/order/' + this.order.order_id + '?token=' + localStorage.token, { edit_items: edit_items }).then(function (res) {
                if (res.body.err_code != 0) {
                    alert(res.body.err_msg);
                } else {
                    this.edit_success_tips = true;
                    var self = this;
                    setTimeout(function () {
                        self.edit_success_tips = false;
                    }, 3000);
                }
            });
        }
    }
};

